@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');

#root {
  width: 100%;
}

body {
  height: auto !important;
  font-family: 'Open Sans';
}

.wrapper {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: stretch;
  width: 100%;
}

#mainSection {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.section-wrapper {
  width: 50%;
}

.section-wrapper > * {
  flex: 1;
}

#preview, #previewHtml, #editor {
  width: 100%;
  border: 1px solid rgb(118, 118, 118);
  min-height: 50vh;
  overflow: auto;
  background-color: #fbf2c0;
  color: #001b2e;
  justify-content: space-between;
}

#clearBtn {
  margin-left: auto;
  justify-content: flex-end;
}

#editor {
  height: 100%;
}

#moon {
  color: white;
}

/* #leftSection {
  position: fixed;
}

#rightSection {
  left: 50vw;
} */

#themeBtn {
  z-index: 999;
  position: fixed;
  right: 0;
  display: inline-grid;
  width: 43px;
  height: 43px;
  overflow: hidden;
}

#themeBtn > * {
  margin: 10px auto;
}

.heading {
  background-color: #dd6e42;
  border: 1px solid #adb6c4;
  border-radius: 3px 3px 0px 0px;
}

img {
  max-width: 100%;
}

@media only screen and (max-width: 768px) {
  .section-wrapper {
    width: 100%;
  }

  #mainSection {
    display: flex;
    flex-direction: column;
  }
}